#rmnGenieWrappingDiv .not-supported-container {
  display: flex;
  align-items: flex-start;
  flex-flow: column nowrap;
  justify-content: center;
  min-height: 290px;
  overflow: hidden;
  position: relative;
  width: 470px;
  z-index: 2;
  background-color: #fbfaff;
  padding: 0;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-content {
    display: flex;
    align-items: flex-start;
    flex-flow: column nowrap;
    justify-content: center;
    flex: 1;
    width: 100%;
    padding: 18px 30px 16px 30px;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 485px;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-content .not-supported-content-inn {
      width: 100%;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-message {
    width: 100%;
    padding: 0 16px;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-title {
    width: 100%;
    padding: 0 16px;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-merchantsList {
    margin: 24px 0 0 0;
    padding: 0 0 16px 0;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-loading {
    height: 180px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
#rmnGenieWrappingDiv .not-supported-container .merchant-block-btn .button-primary {
    line-height: 25px;
}
#rmnGenieWrappingDiv .not-supported-container .merchant-block-btn .button-primary:hover:before {
      box-shadow: 0 22px 28px -22px #de1f72 !important;
}
@media screen and (max-width: 1900px) {
#rmnGenieWrappingDiv .not-supported-container {
    width: 423px;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-content {
      padding: 18px 24px 16px 24px;
      max-height: 435px;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-title {
      padding: 0 8px;
}
}
@media screen and (max-width: 1900px) and (max-width: 1400px) {
#rmnGenieWrappingDiv .not-supported-container {
    width: 399.5px;
}
#rmnGenieWrappingDiv .not-supported-container .not-supported-content {
      padding: 18px 23px 16px 23px;
      max-height: 410px;
}
}
