#rmnGenieWrappingDiv .merchant-search-container {
  display: flex;
  align-items: flex-start;
  flex-flow: column nowrap;
  justify-content: center;
  min-height: 300px;
  overflow: hidden;
  width: 470px;
  padding: 0;
  background-color: #fbfaff;
}
#rmnGenieWrappingDiv .merchant-search-container header {
    background: #fff;
}
#rmnGenieWrappingDiv .merchant-search-body-wrap {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-flow: column nowrap;
  justify-content: center;
  flex: 1;
  width: 100%;
}
#rmnGenieWrappingDiv .merchant-search-body-wrap .search-loading {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    z-index: 5;
    background: rgba(255, 255, 255, 0.65);
}
#rmnGenieWrappingDiv .merchant-search-body {
  display: flex;
  align-items: flex-start;
  flex-flow: column nowrap;
  justify-content: center;
  flex: 1;
  width: 100%;
  padding: 1em 1.75em;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 550px;
}
#rmnGenieWrappingDiv .merchant-search-results {
  width: 100%;
}
#rmnGenieWrappingDiv .search-no-results .secondary-text {
  font-size: 16px;
}
#rmnGenieWrappingDiv .merchant-search-results-list {
  padding: 0 0 16px 0;
}
#rmnGenieWrappingDiv .merchant-block-btn .button-primary {
  line-height: 25px;
}
#rmnGenieWrappingDiv .merchant-block-btn .button-primary:hover:before {
    box-shadow: 0 22px 28px -22px #de1f72 !important;
}
@media screen and (max-width: 1900px) {
#rmnGenieWrappingDiv .merchant-search-container {
    width: 423px;
}
#rmnGenieWrappingDiv .merchant-search-container .merchant-search-body {
      padding: 1em 1.55em;
      max-height: 490px;
}
}
@media screen and (max-width: 1900px) and (max-width: 1400px) {
#rmnGenieWrappingDiv .merchant-search-container {
    width: 399.5px;
}
#rmnGenieWrappingDiv .merchant-search-container .merchant-search-body {
      padding: 1em 1.45em;
      max-height: 440px;
}
}
@media screen and (max-height: 700px) {
#rmnGenieWrappingDiv .merchant-search-body {
    max-height: 440px;
}
}
