#rmnGenieWrappingDiv .list-view-container {
  background: #ffffff;
  width: 470px;
}
#rmnGenieWrappingDiv .list-view-container .list-view-body {
    max-height: 550px;
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
}
#rmnGenieWrappingDiv .list-view-container .cta-wrapper {
    border-bottom: solid 1px #e7e7e7;
    overflow: hidden;
}
#rmnGenieWrappingDiv .list-view-container .bbb-container-gray,
  #rmnGenieWrappingDiv .list-view-container .bbb-container-light,
  #rmnGenieWrappingDiv .list-view-container .bbb-container-dark {
    height: 220px;
}
#rmnGenieWrappingDiv .list-view-container .codes-wrapper {
    padding: 1em 1.75em;
}
#rmnGenieWrappingDiv .list-view-container .codes-wrapper .card {
      line-height: 25px !important;
}
#rmnGenieWrappingDiv .list-view-container .code-section-header {
    text-align: center;
    font-size: 17px;
    margin: 0 0 1em;
}
@media screen and (max-height: 700px) {
#rmnGenieWrappingDiv .list-view-container .list-view-body {
      max-height: 440px;
}
}
@media screen and (max-width: 1900px) {
#rmnGenieWrappingDiv .list-view-container {
    width: 423px;
}
#rmnGenieWrappingDiv .list-view-container .bbb-container-gray,
    #rmnGenieWrappingDiv .list-view-container .bbb-container-light,
    #rmnGenieWrappingDiv .list-view-container .bbb-container-dark {
      height: 198px;
}
#rmnGenieWrappingDiv .list-view-container .code-section-header {
      font-size: 16px;
}
#rmnGenieWrappingDiv .list-view-container .list-view-body {
      max-height: 490px;
}
#rmnGenieWrappingDiv .list-view-container .codes-wrapper {
      padding: 1em 1.55em;
}
}
@media screen and (max-width: 1900px) and (max-width: 1400px) {
#rmnGenieWrappingDiv .list-view-container {
    width: 399.5px;
}
#rmnGenieWrappingDiv .list-view-container .bbb-container-gray,
    #rmnGenieWrappingDiv .list-view-container .bbb-container-light,
    #rmnGenieWrappingDiv .list-view-container .bbb-container-dark {
      height: 187px;
}
#rmnGenieWrappingDiv .list-view-container .code-section-header {
      font-size: 14px;
}
#rmnGenieWrappingDiv .list-view-container .list-view-body {
      max-height: 440px;
}
#rmnGenieWrappingDiv .list-view-container .codes-wrapper {
      padding: 1em 1.45em;
}
}
