#rmnGenieWrappingDiv .card {
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
  margin: 12px 0;
  padding: 1.5em;
}
#rmnGenieWrappingDiv .card .store-logo-container {
    background-color: white;
    flex: 0 0 65px;
    height: 65px;
    margin-right: 1em;
}
#rmnGenieWrappingDiv .card .store-logo-container img {
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.05);
      height: 100%;
      max-width: 100%;
      border-radius: 50%;
      object-fit: scale-down;
}
#rmnGenieWrappingDiv .card .card-content {
    flex: 1 1 auto;
    min-width: 160px;
    word-wrap: break-word;
}
#rmnGenieWrappingDiv .card .title {
    font-weight: bold;
}
#rmnGenieWrappingDiv .card .primary-cta-slot {
    display: flex;
    justify-content: center;
    flex: 0 0 120px;
    margin-left: 1em;
}
#rmnGenieWrappingDiv .card .primary-cta-slot .button-primary {
      margin: 0;
      padding: 0.85em 0;
      width: 100%;
}
#rmnGenieWrappingDiv .card .primary-cta-slot .button-primary:before {
        border: 1px solid transparent;
        transition: 200ms transform, 300ms background-color, 200ms box-shadow, 200ms border-color;
}
#rmnGenieWrappingDiv .card .primary-cta-slot .button-primary.copied {
        color: #de1f72;
}
#rmnGenieWrappingDiv .card .primary-cta-slot .button-primary.copied:before {
          border-color: #de1f72;
          background-color: white;
}
#rmnGenieWrappingDiv .card .primary-cta-slot .button-primary.copied:focus:before, #rmnGenieWrappingDiv .card .primary-cta-slot .button-primary.copied:hover:before {
          transform: none;
          box-shadow: none;
}
#rmnGenieWrappingDiv .card .primary-cta-slot .bbb-loading-spinner {
      margin: 0;
}
#rmnGenieWrappingDiv .card .primary-cta-slot .activated-text {
      font-size: 16px;
      padding: 0;
}
@media screen and (max-width: 1900px) {
#rmnGenieWrappingDiv .card {
    padding: 1.2em;
}
#rmnGenieWrappingDiv .card .primary-cta-slot {
      flex: 0 0 115px;
      margin-left: 0.85em;
}
}
@media screen and (max-width: 1900px) and (max-width: 1400px) {
#rmnGenieWrappingDiv .card {
    padding: 1em;
}
#rmnGenieWrappingDiv .card .primary-cta-slot {
      flex: 0 0 110px;
}
}
